@import url(https://fonts.googleapis.com/css?family=Lato|MedievalSharp);
body {
  margin: 0;
  font-family: "Lato", sans-serif;
  background: #333;
  overflow: hidden;
  height: 100%;
  width: 100%; }

a {
  color: #baba45; }

#loading-container {
  display: none; }

#app-container {
  display: block; }

.map-container {
  background: #333;
  height: 100vh;
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
  /** Leaflet Style Overrides **/ }
  .map-container .leaflet-popup {
    bottom: 0; }
  .map-container .leaflet-popup-content {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer; }
  .map-container .leaflet-popup-content-wrapper,
  .map-container .leaflet-popup-tip {
    background: #333;
    color: #faebd7;
    text-align: center; }
  .map-container .leaflet-control-zoom {
    border: none; }
  .map-container .leaflet-control-zoom-in,
  .map-container .leaflet-control-zoom-out {
    background: #333;
    color: #faebd7;
    border: none; }
  .map-container .leaflet-control-attribution {
    display: none; }
  @media (max-width: 600px) {
    .map-container .leaflet-bottom {
      bottom: calc(80px + 10px); } }

.info-title {
  font-family: "MedievalSharp", cursive;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #faebd7;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%; }
  .info-title h1 {
    letter-spacing: 0.3rem;
    max-width: 100%;
    padding: 20px;
    text-overflow: ellipsis;
    text-align: center; }

.info-content {
  padding: 25px 8% 24px 8%;
  margin: 0 auto;
  background: #333;
  overflow-y: scroll;
  font-size: 1rem;
  line-height: 1.25em;
  font-weight: 300; }

.info-container {
  position: absolute;
  overflow-y: hidden;
  bottom: 0;
  left: 24px;
  z-index: 1000;
  background: #222;
  width: 500px;
  height: 60vh;
  color: #faebd7;
  transition: all 0.4s ease-in-out;
  transform: translateY(calc(100% - 80px)); }

.info-container.info-active {
  transform: translateY(0); }

.info-body {
  margin-top: 80px;
  overflow-y: scroll;
  overflow-x: hidden;
  position: relative;
  height: 80%; }

.info-footer {
  font-size: 0.8rem;
  font-family: "Lato", sans-serif;
  padding: 8%;
  text-align: center;
  text-transform: uppercase; }

.blog-link {
  letter-spacing: 0.1rem;
  font-weight: bold; }

@media (max-width: 600px) {
  .info-container {
    left: 0;
    width: 100%;
    height: 80vh; } }

.search-container {
  position: absolute;
  top: 24px;
  left: 24px;
  background: transparent;
  z-index: 1000;
  color: #faebd7;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
  /* When the input field gets focus, change its width to 100% */ }
  .search-container input[type="text"] {
    width: 60px;
    transition: width 0.4s ease-in-out;
    cursor: pointer; }
  .search-container input[type="text"]:focus {
    width: 500px;
    outline: none;
    cursor: text; }

.search-results {
  margin-top: 4px;
  border-radius: 4px;
  background: #222; }
  .search-results div {
    padding: 16px;
    cursor: pointer; }
  .search-results div:hover {
    background: #333; }

.search-bar,
.search-input {
  height: 60px; }

.search-input {
  background-color: #222;
  color: #faebd7;
  border: 3px #333 solid;
  border-radius: 4px;
  font-size: 1rem;
  padding: 0 15px;
  background-image: url("https://storage.googleapis.com/material-icons/external-assets/v4/icons/svg/ic_search_white_18px.svg");
  background-position: 20px 17px;
  background-size: 24px 24px;
  background-repeat: no-repeat;
  padding-left: 60px; }

@media (max-width: 600px) {
  .search-container {
    width: 100%;
    top: 0;
    left: 0; }
    .search-container .search-input {
      border-radius: 0; }
    .search-container .search-results {
      margin-top: 0;
      border-radius: 0; } }

